/*
 *  Define the navigation styles.
 */


.navigation {
  position: relative;
  top: 0;
  left: 0;
  z-index: 999;
  width: 100%;
  height: auto;
  padding: 15px;
  text-align: center;
  
  @include min($tablet) {
    padding: 15px 100px;
  }
}

.logo {
  display: inline-block;
  z-index: 100;
  text-align: center;

  svg {
    width: 180px;
    fill: $black;
    stroke: $black;

    @include min($tablet) {
      width: 220px;
    }

    * {
      fill: $black;
      stroke: $black;
    }
  }
}

.nav-list {
  float: right;
  padding-top: 34px;
}

.nav-list__item {
  display: inline-block;
  vertical-align: top;
  list-style: none;
}

.nav-link {
  margin-top: 17px;
  color: $black;
  // text-transform: uppercase;
  letter-spacing: 1px;
  font-family: $sans;
  font-weight: 400;
  font-size: 1.4rem;
  webkit-font-smoothing: auto;  

  @include min($tablet) {
    margin-top: 24px;
    font-size: 1.7rem;
  }

  &.nav-link--first {
    float: left;
  }

  &.nav-link--last {
    float: right;
  }
}

.cart-contents {

  span {
    position: relative;
    top: -8px;
    left: -4px; 
    display: inline-block;
    width: 22px;
    height: 22px;
    border-radius: 100%;
    background: #f7f7f7;
    color: $black;
    font-size: 1.3rem;
  }
}
