/*
 *  Define the shop styles.
 */

.filters {
  padding: 0 100px;
  border-top: 1px solid $light-grey;
  letter-spacing: 1px;
  font-weight: 400;
}

.filter-label {
  display: inline-block;
  margin-right: 50px;
  letter-spacing: 1px;
  color: $grey;
  font-size: 1.6rem;
  font-weight: 700;
  font-family: $mono;
  -webkit-font-smoothing: auto;
}

.filter-list {
  display: inline-block;
  margin: 0;
  padding: 20px 0 20px;
}

.filter-list-item {
  display: inline-block;
  margin-right: 30px;
  padding: 5px 0px;
  list-style: none;
}

.filter-list-item--active {
  border-bottom: 2px solid $black;
}

.filter-sort {
  float: right;
  padding: 24px 0 16px;
  animation: fadeIn 1000ms 1;

  .filter-label {
    margin-right: 25px;
  }

  .selectize-input {
    padding: 8px 15px;

    &:after {
      content: '';
      position: absolute;
      transition: border-color 200ms ease;
      top: 17px;
      right: 15px;
      height: 0;
      width: 0;
      border-left: 7px solid transparent;
      border-right: 7px solid transparent;
      border-top: 8px solid rgba($black, 0.8);
      border-radius: 3px;
    }

    input {
      display: none;
    }
  }

  .selectize-control {
    width: 210px;
  }

  .selectize-dropdown {
    position: absolute;
    left: -1px !important;
    width: 101% !important;
    z-index: 100;
  }

  .selectize-dropdown-content {

    .option {
      transition: all 200ms ease;
      padding: 10px 15px 10px 16px;
      background: $black;
      color: $light-grey;
      cursor: pointer;

      &:hover {
        background: $grey;
        color: $white;
      }
    }
  }

  form {
    position: relative;
    display: inline-block;
    vertical-align: top;
    margin-top: -8px;
    border: 1px solid $light-grey;
    transition: border-color 300ms ease;

    &:hover,
    &:active {
      border-color: $grey;

      .selectize-input::after {
        border-top: 9px solid rgba($black, 0.99);
      }
    }
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: translateY(25px);
  }

  60% {
    opacity: 0;
    transform: translateY(25px);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
